html {
  font-family: Roboto, sans-serif;
  color: #060B35;
  box-sizing: border-box;
}

body {
  margin: 0;
}

.card {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  border-radius: 5px;
  border: 1px solid #F3F3F3;
  justify-content: center;
  width: 200px;
  padding: 32px 16px 16px;
}
.card__img {
  height: 134px;
  margin: 0 auto;
  margin-bottom: 40px;
}

.card__product-name {
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  margin: 0;
  margin-bottom: 4px;
}
.card__product-code {
  font-size: 10px;
  line-height: 14px;
  color: #616070;
  margin: 0;
  margin-bottom: 16px;
}

.card__reviews {
  display: flex;
  justify-content: space-between;
  margin-bottom: 24px;
}
.card__stars {
  display: flex;
  gap: 4px;
}
.card__star {
  background-image: url(../images/star.svg);
  background-repeat: no-repeat;
  background-position: center;
  width: 16px;
  height: 16px;
}
.card__star:nth-child(-n+4) {
  background-image: url(../images/star-active.svg);
}
.card__reviews-number {
  display: flex;
  justify-self: flex-end;
  font-size: 10px;
  line-height: 14px;
  align-items: flex-end;
  margin: 0;
}

.card__price {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 16px;
}
.card__price-text {
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
  color: #616070;
}
.card__price-value {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #00ACDC;
  border-radius: 5px;
  background-color: #00ACDC;
  width: 166px;
  height: 40px;
}
.card__button-text {
  font-weight: 700;
  font-size: 14px;
  line-height: 16px; /* 114.286% */
  color: #fff;
  text-transform: uppercase;
  text-decoration: none;
  border-radius: 5px;
  padding: 12px 70px;
}
.card__button-text:hover {
  background-color: #fff;
  color: #00ACDC;
}